@import url(https://fonts.googleapis.com/css?family=Anton|Lobster|Red+Hat+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 0;
}

.logo {
  position: absolute;
  top: 0;
  left: 20px;
  height: 50px;
  overflow: hidden;
  margin: 0;
  padding: 0;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
}

.logo__szymski, .logo__BEATS {
  display: inline-block;
  padding: 4px;
  line-height: 50px;
  height: 50px;
  margin: 0;
  font-size: 1.2em;
}

.logo__szymski {
  background: white;
  color: black;
  font-family: "Lobster", sans-serif;
  float: left;
}

.logo__BEATS {
  background: #4a4b46;
  color: white;
  font-style: bold;
  font-family: "Red Hat Display", sans-serif;
  float: right;
}

.menu {
  position: absolute;
  top: 0;
  right: 15px;
  font-family: "Red Hat Display", sans-serif;
}

.menu:hover {
  cursor: pointer;
}

.menu__title {
  display: none;
}

.menu__checker {
  position: fixed;
  top: 1rem;
  right: 1rem;
  height: 24px;
  width: 40px;
  z-index: 9999;
  cursor: pointer;
  -webkit-transition: 1s;
  transition: 1s;
}

.menu__span {
  position: absolute;
  top: 0;
  left: 0;
  height: 3px;
  width: 100%;
  background: white;
  z-index: 1000;
  border: 1px solid white;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
}

.menu__span:nth-of-type(2) {
  top: 10px;
}

.menu__span:nth-of-type(3) {
  top: 20px;
}

.menu__span_open:nth-of-type(1) {
  -webkit-transform: translateY(10px) translateX(0) rotate(45deg);
  transform: translateY(10px) translateX(0) rotate(45deg);
}

.menu__span_open:nth-of-type(2) {
  opacity: 0;
}

.menu__span_open:nth-of-type(3) {
  -webkit-transform: translateY(-10px) translateX(0) rotate(-45deg);
  transform: translateY(-10px) translateX(0) rotate(-45deg);
}

.menu__list {
  list-style: none;
  color: white;
}

.menu__li {
  font-size: 3em;
  margin-top: 50px;
}

.menu__li:hover {
  color: silver;
}

.menu__overlay {
  position: fixed;
  top: -100%;
  left: 0;
  height: 100vh;
  width: 100%;
  font-family: "Lobster", helvetica;
  background: rgba(0, 0, 0, 0.9);
  z-index: 999;
  opacity: 0;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease;
}

.menu__overlay_open {
  top: 0;
  opacity: 1;
}

.site {
  overflow: hidden;
  margin: 0;
  width: 100%;
}

body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/szymskiBeats/static/media/bgSs.98a103d1.jpg) no-repeat center center;
  background-image: url(/szymskiBeats/static/media/bgSs.98a103d1.jpg);
  background-size: 500%;
  background-position: 10% 30%;
  background-attachment: fixed;
}

.secondSection {
  display: none;
}

.mainSection {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto;
  box-shadow: 3px 3px 8px 0px rgba(0, 0, 0, 0.75);
  -webkit-transition: all 1s ease-out 0.2s;
  transition: all 1s ease-out 0.2s;
}

.bgOrange {
  background: #ff9b17;
  background: -webkit-radial-gradient(circle, #ff9b17 0%, #ff631a 100%);
  background: radial-gradient(circle, #ff9b17 0%, #ff631a 100%);
}

.bgGreen {
  background: #04c9a6;
  background: -webkit-radial-gradient(circle, #04c9a6 0%, #0be474 100%);
  background: radial-gradient(circle, #04c9a6 0%, #0be474 100%);
}

.bgBlue {
  background: #282dff;
  background: -webkit-radial-gradient(circle, #282dff 0%, #b410f4 100%);
  background: radial-gradient(circle, #282dff 0%, #b410f4 100%);
}

.section {
  overflow: hidden;
  margin-top: 25px;
  margin-bottom: 10px;
  padding-bottom: 15px;
  width: 100%;
  min-height: 97vh;
}

.scrollText {
  height: 60px;
  width: 100%;
  font-family: "Lobster";
  font-size: 1em;
  margin: 30px auto 0 auto;
  cursor: pointer;
}

.scrollText p {
  color: white;
  margin: 0 auto;
  text-align: center;
}

.scrollText img {
  display: block;
  margin: 0 auto;
  height: 40px;
  width: 50px;
  -webkit-animation-name: scrollText;
  animation-name: scrollText;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
  animation-direction: normal;
}

.tracks {
  margin-top: 50px;
}

.tracks iframe {
  padding-top: 25px;
  width: 100%;
  overflow: hidden;
  display: block;
  height: 20px;
  margin: 30px auto 0 auto;
}

.iframeBg {
  background: white;
  height: 70px;
  box-shadow: 2px 2px 7px 0px rgba(0, 0, 0, 0.75);
}

.sectionHeader, .secondSection__header {
  font-family: "Lobster", sans-serif;
  font-size: 1.2em;
  margin: 0 auto;
  text-align: center;
  margin-top: 15px;
  margin-bottom: 25px;
  background: #4a4b46;
  color: white;
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
}

.aboutMe, .contact {
  font-family: "Lobster", sans-serif;
  color: white;
}

.aboutMe__text, .contact__text {
  padding: 15px 0;
  width: 90%;
  text-align: center;
  margin: 0 auto;
  border-radius: 2%;
  text-shadow: 1px 1px 1px #4a4b46;
  font-size: 1.1em;
}

.aboutMe__logo {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  border: 3px solid white;
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.75);
  background: url(/szymskiBeats/static/media/justdoit.9a0bc422.jpg) no-repeat 10% 30%/300px;
  display: block;
  margin: 0 auto;
}

.contact .scrollText {
  height: 60px;
  width: 100%;
  font-family: "Lobster";
  font-size: 1em;
  margin: 30px auto 0 auto;
  cursor: pointer;
  margin-top: 150px;
}

.contact .scrollText p {
  color: white;
  margin: 0 auto;
  text-align: center;
}

.contact .scrollText img {
  display: block;
  margin: 0 auto;
  height: 40px;
  width: 50px;
  -webkit-animation-name: scrollTextRev;
  animation-name: scrollTextRev;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-direction: normal;
  animation-direction: normal;
}

.contact__container {
  width: 90%;
  margin: 0 auto;
  border-radius: 2px;
}

.contact__text {
  margin-top: 150px;
}

.contact__mediaContainer {
  height: 50px;
  width: 250px;
  text-align: center;
  margin: 10px auto;
}

.contact__mediaContainer span {
  line-height: 50px;
  height: 50px;
  font-size: 2em;
  float: right;
}

.contact__phoneIco, .contact__mailIco {
  height: 50px;
  width: 50px;
  float: left;
}

/*************************
Animations
*************************/
@-webkit-keyframes scrollText {
  from {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(5px);
    transform: translateY(5px);
    opacity: 0;
  }
}

@keyframes scrollText {
  from {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    opacity: 1;
  }
  to {
    -webkit-transform: translateY(5px);
    transform: translateY(5px);
    opacity: 0;
  }
}

@-webkit-keyframes scrollTextRev {
  0% {
    -webkit-transform: translateY(5px) rotate(0deg);
    transform: translateY(5px) rotate(0deg);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateY(5px) rotate(-180deg);
    transform: translateY(5px) rotate(-180deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(0px) rotate(-180deg);
    transform: translateY(0px) rotate(-180deg);
    opacity: 0;
  }
}

@keyframes scrollTextRev {
  0% {
    -webkit-transform: translateY(5px) rotate(0deg);
    transform: translateY(5px) rotate(0deg);
    opacity: 1;
  }
  50% {
    -webkit-transform: translateY(5px) rotate(-180deg);
    transform: translateY(5px) rotate(-180deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(0px) rotate(-180deg);
    transform: translateY(0px) rotate(-180deg);
    opacity: 0;
  }
}
/*# sourceMappingURL=main.css.map */
@media screen and (min-width: 1024px) {
  body:before {
    background-size: 200%;
    background-position: 0 0;
    background-attachment: fixed;
  }
  .logo {
    position: fixed;
    left: 15%;
    z-index: 9999;
  }
  .menu__checker {
    display: none;
  }
  .menu__overlay {
    top: 0;
    left: 70%;
    opacity: 1;
    height: 58px;
    background: none;
  }
  .menu__list {
    margin: 0;
  }
  .menu__li {
    font-size: 1em;
    display: inline-block;
    line-height: 58px;
    margin: 0 2px;
    padding: 0 4px;
    background: #4a4b46;
    box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.75);
  }
  .mainSection {
    position: absolute;
    top: 0;
    left: 0;
    width: 62%;
    overflow: hidden;
    box-shadow: 1px 1px 8px 0px rgba(0, 0, 0, 0.75);
  }
  .sectionHeader {
    display: none;
  }
  .tracks, .aboutMe, .contact {
    -webkit-transition: all 0.2s ease-out 0.1s;
    transition: all 0.2s ease-out 0.1s;
    z-index: 100;
  }
  .tracks {
    margin-top: 0;
    padding-top: 50px;
  }
  .tracks iframe {
    padding-top: 0;
    height: 117px;
    -webkit-transition: all 0.2s ease-out 0.2s;
    transition: all 0.2s ease-out 0.2s;
    box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.75);
    width: 98%;
  }
  .iframeBg {
    background: none;
    height: auto;
    box-shadow: none;
  }
  .contact {
    padding-top: 70px;
  }
  .aboutMe {
    padding-top: 80px;
  }
  .aboutMe__logo {
    height: 250px;
    width: 250px;
    background-size: 400px;
  }
  .aboutMe__text, .contact__text {
    width: 95%;
    box-shadow: none;
    text-shadow: 1px 1px 8px #4a4b46;
    font-size: 1.2em;
  }
  .mainSection, .secondSection {
    border-radius: 2px;
  }
  .secondSection {
    display: block;
    position: fixed;
    top: 150px;
    right: 10%;
    width: 33%;
    height: 320px;
    box-shadow: 3px 3px 6px 0px rgba(0, 0, 0, 0.3);
    -webkit-transition: all 0.5s ease-out 0.2s;
    transition: all 0.5s ease-out 0.2s;
    z-index: 50;
  }
  .secondSection__header {
    -webkit-transition: all 1s ease-out 0.2s;
    transition: all 1s ease-out 0.2s;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    font-size: 3.5em;
    background: none;
    line-height: 320px;
    margin: 0;
    text-shadow: 1px 1px 8px #4a4b46;
    color: white;
  }
}
/*# sourceMappingURL=mainM.css.map */
